import { Injectable } from '@angular/core';
import { COLUMNS, HOSO } from './hoso.model';
import { Workbook } from "exceljs";
import { XuLyDuLieuXuatAnki } from './class/xu-ly-du-lieu-xuat-anki';
import { Anki } from './class/genankijs';
import { HttpClient } from "@angular/common/http";
import { BehaviorSubject } from 'rxjs';

import { ToastService } from '../service/toast-service.service';
/**
 * Lấy dữ liệu của file excel hoặc google sheet
 * Dữ liệu cột được bỏ vào hàm trong XuLyDuLieuXuatAnki.ts
 * @returns: danh sách dữ liệu có thể add to decks và ra hàng
 */
@Injectable({
  providedIn: 'root'
})
export class GetExcelDataService {
  public loadinggetcolumns$ = new BehaviorSubject<boolean>(false)
  constructor(
    public http?: HttpClient,
    private toastController?: ToastService,
  ) { }
  /* Tải file từ link và xuất ra */
  async getGoogleSheet(hoso: HOSO) {
    /* Nếu link là từ google sheet → chuyển thành link xlsx
    Nếu không thì mình xài luôn
    */
    //@ts-ignore
    let myurl = this.chuyenUrlToXlsx(hoso.ggsheetlinkpublishtoweb)
    //@ts-ignore
    return this.http.get(myurl, { responseType: "arraybuffer" }).toPromise().then(async arrayBuffer => {
      return this.createAnkiFile(arrayBuffer, hoso)
    })
  }

  /* Xử lý arrayBuffer sau khi tải về từ link, hoặc lấy từ file local */
  async createAnkiFile(arrayBuffer: any, hoso: HOSO) {
    /**
     * Xử lý cột trong hồ sơ
     */
    //@ts-ignore
    let fldsChoModel = hoso.columns.map((v, i) => {
      return { 'name': v.name }
    })
    /** Thêm cột id cho nó để hiện thị, guid để định danh note */
    fldsChoModel.push({ name: "id" })
    // console.log(fldsChoModel)
    if (arrayBuffer) {
      console.log(arrayBuffer)
      let wb = new Workbook();
      return wb.xlsx.load(arrayBuffer).then(async workbook => {
        let anki = new Anki()
        let ankipkg = anki.Package()
        let crtime = new Date().getTime()
        // console.log(fldsChoModel)
        let hosohtml = this.stylehtmlToColumns(hoso)
        let ankiModel = anki.ClozeModel({
          name: "Drquochoai (ExcelToAnki website), AutoCloze",
          id: 394446896,
          flds: fldsChoModel,
          tmpls: [{
            name: "drquochoai",
            qfmt: hosohtml.front,
            afmt: hosohtml.back
          }],
          css: hoso.css,
          mod: crtime
        })

        let deckIdList: number[] = []
        hoso.stats = {
          sheets: 0,
          notes: 0
        }
        workbook.eachSheet(async (sheet, id) => {
          // Bỏ qua sheet bắt đầu với #
          if (!sheet.name.startsWith("$") && !sheet.name.startsWith("!") && !sheet.name.startsWith("Form Responses")) {
            /**
             * Gửi toàn bộ dữ liệu của Sheet này sang Class xử lý dữ liệu
             */
            /* this.toastController?.presentToast({message: `\n #${id}. Bắt đầu xử lý trang tính "${sheet.name}"`}) */
            console.info(`\n #${id}. Bắt đầu xử lý trang tính "${sheet.name}"
            `)
            //@ts-ignore
            hoso.stats.sheets += 1
            // console.log("sheet.getSheetValues, trả về okie!", sheet.getSheetValues())
            let formated = new XuLyDuLieuXuatAnki(sheet.getSheetValues(), hoso).htmlArray()
            // console.log(formated)
            // console.log(sheet.name)
            /**
             * 1. Tạo Deck và loop quanh formated để chèn vào deck đó
             * 2. Tên deck = tên hồ sơ :: sheet name
             */
            // console.log(formated )
            let deckname = hoso.name + "::" + sheet.name
            let deckid = new Date().getTime() // Tạo deckname unique

            while (deckIdList.includes(deckid)) {
              await new Promise(resolve => setTimeout(resolve, 2));
              deckid = new Date().getTime()
            }
            deckIdList.push(deckid)

            let deck = anki.Deck(deckid, deckname)

            /**
             * Tạo guid cho note = hosoname :: sheetname :: line 11
             * Tạo link cho deck
             * Tạo thêm link ?
             */
            // console.log(formated)
            /** Tiến hành thêm deck */
            /* Bỏ qua hàng 1, vì Nó là title ; Không sử dụng filter vì nó làm thay đổi index
               Nếu cột 1 trống -> bỏ qua luôn (cột 1 là front row[1])
               Lỗi import ankiDroid các hàng trống cột đầu
            */
            //  let count = 0
            let countafter = 0

            // console.log("Trước map", formated)

            formated = formated
              .map((row, indexRow) => {
                row!.push(indexRow)
                return row
              })
            // console.log("Sau map", formated)
            formated = formated
              .filter((row) => {
                // count+=1
                // console.log(row, indexRow)
                if (row![1] != undefined) {
                  countafter += 1
                  return true
                } else return false
              })
            // console.log(formated)
            console.log("Sheet", `"${sheet.name}"`, "có tổng cộng", countafter - 1, "hàng có dữ liệu (tức cột 1 không trống)")
            hoso!.stats!.notes! += countafter - 1
            formated.forEach((row, indxRow) => {
              // console.log(row)
              if (indxRow > 0) {
                /** 1. Xóa vị trí 0, vì nó là empty, tương ứng cột 0 trong excel */
                row!.shift()
                /** 2. Tạo guid cho note = hosoname :: sheetname :: line 1
                 * line đang lưu ở cột cuối cùng của row.
                 * Gọi hàm pop() vừa xóa cột cuối, vừa trả về giá trị của cột đó
                */
                let guid = `${deckname}::line ${row!.pop()}`
                // row.pop()
                /** 3. Thêm biến id cho columns */
                row!.push(guid)
                // console.log(row)
                /** 4. Add note */
                // row.fill("1")
                // Bug tags: có space sẽ bị tách ra
                deck.addNote(ankiModel.note(row, [deckname!.replaceAll(" ", "_")], guid))
                // console.log(row)
              }
            })
            /** 5. Add deck */
            // console.log(deck)
            ankipkg.addDeck(deck)

          } else {
            if (!sheet.name.startsWith("Form Responses")) {
              console.warn(`#${id}. Trang tính "${sheet.name}" được bỏ qua do bắt đầu với ${sheet.name.slice(0, 1)}`)
            }

          }
        })
        // console.log(ankipkg)
        ankipkg.writeToFile(`${hoso.name}-${crtime}-by drquochoai.apkg`)
        hoso.creatingAnki = false
        return "createok"
      })
    } else {
      console.log("Lỗi file excel")
      return "createnotok"
    }
  }
  stylehtmlToColumns(hoso: HOSO) {
    //{{cloze:Front}}; trước nó có thể cloze nên mình chỉ đóng ngoặc
    let start = 1, to = 18278;
    // console.log(hoso.columns)
    let hosohtml = {
      front: hoso.front,
      back: hoso.back
    }
    while (start <= to) {
      try {
        hosohtml.front = hosohtml!.front!.replaceAll("$" + this.styleNumberToLetters(start) + "}",
          hoso!.columns![start - 1].name + "}")
        hosohtml.back = hosohtml!.back!.replaceAll("$" + this.styleNumberToLetters(start) + "}",
          hoso!.columns![start - 1].name + "}")
        start++
      } catch (error) {
        break
      }
    }
    return hosohtml
  }
  // https://stackoverflow.com/questions/11089399/count-with-a-b-c-d-instead-of-0-1-2-3-with-javascript
  styleIndexFromLetters(str: string) {
    str = str.toUpperCase()
    let out = 0, len = str.length, pos = len;
    while (--pos > -1) {
      out += (str.charCodeAt(pos) - 64) * Math.pow(26, len - 1 - pos);
    }
    return out;
  }
  styleNumberToLetters(num: number): any {
    var mod = num % 26,
      pow = num / 26 | 0,
      out = mod ? String.fromCharCode(64 + mod) : (--pow, 'Z');
    return pow ? this.styleNumberToLetters(pow) + out : out;
  }


  chuyenUrlToXlsx(url: string) {
    let myurl = url
    if (myurl.indexOf("/pubhtml")) {
      myurl = url.replace("/pubhtml", "/pub?output=xlsx")
    }
    return myurl
  }
  /**
   * 
   * @param hoso 
   * @returns Trả về hoso đã chỉnh sửa tên cột theo file excel
   */
  async getExcelColumns(hoso: HOSO) {
    // console.log("call getExcelColumns")
    // Nếu không phải là File máy tính => là link google
    if (!hoso.isFromLocalFile) {
      this.loadinggetcolumns$.next(true)

      // myurl = this.chuyenUrlToXlsx(myurl) + "&gid=0"
      let myurl = this.chuyenUrlToXlsx(hoso!.ggsheetlinkpublishtoweb!)
      // console.log(myurl)
      return await this.http!.get(myurl, { responseType: "arraybuffer" }).toPromise().then(async (arrayBuffer) => {
        this.preProcessColumns(hoso, arrayBuffer)
      }).catch(error => {
        console.log("Lỗi tải excel từ internet; ", error)
        this.loadinggetcolumns$.next(false)
        return hoso
      })
    } else {
      // this.toastController?.presentToast({message: "Xin chọn file để tải cột"})

    }
  }

  async getFileArrayBuffer(hoso: HOSO, arrayBuffer: ArrayBuffer) {
    let wb = new Workbook();
    return await wb.xlsx.load(arrayBuffer).then(workbook => {
      // let ws = workbook.worksheets[0]
      // this.preProcessColumns(hoso, arrayBuffer)
      return this.preProcessColumns(hoso, arrayBuffer)
    })
  }
  async preProcessColumns(hoso: HOSO, arrayBuffer: any) {
    if (arrayBuffer) {
      let wb = new Workbook();
      return await wb.xlsx.load(arrayBuffer).then(workbook => {
        // lấy sheet đầu tiên
        let ws = workbook.worksheets[0]
        let colcuoi: COLUMNS[] | undefined = []
        // Lấy hàng 1, sau đó loop qua từng hàng
        ws.getRow(1).eachCell((cel, col) => {
          // hoso.columns[col-1].name = cel.value.toString()
          // console.log(cel.value.toString())
          // console.log(hoso.columns[col-1].name)
          let thiscol: COLUMNS = { name: null }
          // console.log(cel.value)
          thiscol["name"] = cel!.value!.toString()
          // console.log(thiscol.name)
          try {
            thiscol.replace = hoso!.columns![col - 1].replace
          } catch (error) {
            console.warn(thiscol['name'], "Không có biến replace")
          }

          try {
            thiscol.copyxuong = hoso!.columns![col - 1].copyxuong
          } catch (error) {
            console.warn(thiscol['name'], "Không có biến copyxuong")
          }

          try {
            thiscol.isinfront = hoso!.front!.indexOf(thiscol.name) > -1
          } catch (error) {
            console.error(thiscol['name'], "Lỗi kiểm tra columns có trong Front.html không")
          }

          try {
            thiscol.isinback = hoso!.back!.indexOf(thiscol.name) > -1
          } catch (error) {
            console.error(thiscol['name'], "Lỗi kiểm tra columns có trong Front.html không")
          }

          colcuoi!.push(thiscol)
        })
        hoso.columns = colcuoi
        this.loadinggetcolumns$.next(false)

        return hoso
        // console.log(values)
      })
    } else return false
  }
}
