import { HOSO } from "./hoso.model";

export const HoSoMacDinh = {
  name: "Hồ sơ mặc định by drquochoai",
  description: `Style card mặc định.
Ấn vào để chỉnh sửa, hoặc ấn tạo bản sao để copy.`,
  ggsheetlinkpublishtoweb: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRcpcYep2QHyna5agDKp_struUYY0vw616nHKdt_NoH1EFCcCGaZS1lBkwC04zC1RE8lfxil7fvG3Q5/pubhtml',
  ggsheeteditlink: "https://docs.google.com/spreadsheets/d/1vOLT9pRVGXHI7gJtjnCqvusiAYTY2o2VW5rSoSJyOG0/edit",
  columns: [
    { name: "Front" },
    { name: "Extra note" },
    { name: "Title", copyxuong: true, replace: true },
    { name: "Footer", copyxuong: true, replace: true }
  ],
  clozeAllInOne: true,
  replace: `##a. = <br/> A. 
##b. = <br/> B. 
##c. = <br/> C. 
##d. = <br/> D. 
##e. = <br/> E. 
 *Có* = *Có* (Từ "có" được thay thế replace)`,
  front: `<meta name="referrer" content="no-referrer">
  <div class="blog-card">
  <div class="description">
    <h1>{{$C}}</h1>
    <p class="front withline"> {{cloze:$A}}</p>
  </div>
  <div class="footer">
    <p class="withline">{{$D}}</p>
    <div class="noteid"><b>Note ID:</b> {{id}} </div>
  </div>
</div>
<!-- /*CHỈNH STYLES ONLINE https://codepen.io/drquochoai/pen/NWpbjEb */ -->`,
  back: `<meta name="referrer" content="no-referrer">
  <div class="blog-card">
  <div class="description">
    <h1>{{$C}}</h1>
    <p class="front withline">{{cloze:$A}}</p>
    <div>
      <p class="dr_extranote">{{$B}}</p>
    </div>
  </div>
  <div class="footer">
    <p class="withline">{{$D}}</p>
    <div class="noteid"><b>Note ID: 2</b> {{id}}</div>
  </div>
</div>
`,
  css: `@charset "UTF-8";
  :root {
    --line-color: #5ad67d;
    --font-size: 1.5rem;
  }
  * {
    box-sizing: border-box;
    text-align: left !important
  }
  body {
    background: #f1f1f1;
    margin: 10px;
  }
  .cloze {
    font-weight: bold;
    color: #0088ff;
  }
  .blog-card {
    display: flex;
    flex-direction: column;
    margin: 1rem auto;
    box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.1);
    margin-bottom: 1.6%;
    background: #fff;
    line-height: calc(1.4 * var(--font-size));
    font-family: sans-serif;
    border-radius: 5px;
    overflow: hidden;
    z-index: 0;
  }
  .blog-card .description {
    padding: 1rem;
    background: #fff;
    position: relative;
    z-index: 1;
  }
  .blog-card .description h1,
  .blog-card .description h2 {
    font-family: sans-serif;
  }
  .blog-card .description h1 {
    line-height: 0.9;
    margin: 0;
    font-size: calc(1.5 * var(--font-size));
  }
  .blog-card p {
    position: relative;
    margin: 1rem 0 0;
    font-size: calc(1 * var(--font-size));
  }
  .blog-card p.withline {
    margin-top: 1.25rem;
  }
  .blog-card p.withline:before {
    content: "";
    position: absolute;
    height: 5px;
    background: var(--line-color);
    width: 100%;
    top: -0.75rem;
    border-radius: 3px;
  }
  .footer {
    padding: 1rem;
    padding-top: 0px;
    font-size: calc(0.8 * var(--font-size));
    font-weight: 200;
    text-transform: uppercase;
    color: #a2a2a2;
    line-height: calc(1.4 * 0.7 * var(--font-size));
  }
  .footer p {
    font-size: calc(0.7 * var(--font-size));
  }
  .noteid {
    text-transform: none;
  }
  .hide {
    display: none;
  }
  .bold {
    font-weight: bold;
  }
  .italic {
    font-style: italic;
  }
  .underline {
    text-decoration: underline;
  }
  .strike {
    text-decoration: line-through;
  }
  .strike.underline {
    text-decoration: line-through underline;
  }
  .image,
  .youtube {
    max-width: 70%;
    clear: both;
    display: block;
    margin-left: 15%;
    width: auto;
    height: auto;
  }
  
  @media (max-width: 600px) {
    .image,
    .youtube {
      max-width: 90%;
      margin-left: 5%;
    }
  }
  
  .nightMode body,
  .nightMode .blog-card,
  .nightMode .blog-card,
  .nightMode .blog-card .description {
    background-color: transparent !important;
  }
  `
} as HOSO;