declare const Package: any
declare const Deck: any

export interface AnkiModel {
    id?: number;
    name?: string;
    flds?: {
        name?: string | null
    }[];
    tmpls?: {
        name?: string,
        qfmt?: string,
        afmt?: string,
        did?: 0
    }[];
    css?: string,
    mod? : number
}

declare const Model: any
declare const ClozeModel: any

export class Anki {
    constructor() { }
    Model(arg: AnkiModel) {
        return new Model(arg)
    }
    ClozeModel(arg: AnkiModel) {
        return new ClozeModel(arg)
    }
    Package(arg?: undefined) {
        return new Package(arg)
    }
    Deck(id: any, name: string, desc: string = "Deck created with Excel to anki.\nby drquochoai with love 🥰💖\nAccess https://exceltoanki.github.io for more detail.\nJoin Zalo group to get best support: https://zalo.me/g/njgdex630") {
        return new Deck(id, name, desc)
    }
}
