import { Injectable } from '@angular/core';
import { Storage } from "@ionic/storage-angular";
import { CAIDAT } from '../CAIDAT';

@Injectable({
  providedIn: 'root'
})
export class MenuTagsService {
  tagsList = new Set()
  skey = CAIDAT.skey
  private _storage: Storage | null = null;

  filterBigList: string[] = []
  constructor(
    private storage: Storage
  ) {
    this.init()
  }

  async init() {
    const storage = await this.storage.create()
    this._storage = storage
    // this.tagsList.add("zsd")
    await this._storage.get(CAIDAT.tags.tagsList).then(re => {
      if (re) {
        //@ts-ignore
        re.forEach(element => {
          this.tagsList.add(element)
        });
      }
    })
    await this._storage.get(CAIDAT.tags.savedBigList).then(re => {
      if (re) {
        // console.log(re)
        //@ts-ignore
        re.forEach(element => {
          this.filterBigList.push(element)
        });
      }
    })
  }
 
  async addTags(tagsinput: string) {
    let tags = tagsinput.split(",")
    tags.forEach((val, ind) => {
      this.tagsList.add(val.trim())
    })
    await this.updateTagsList()
  }
  deleteGiuHoSo(tag: string) {
    this.tagsList.delete(tag)
    this.updateTagsList()
  }
  async deleteXoaTagHoSo(tag: string) {
    this.deleteGiuHoSo(tag)
    /** Loop qua từng hồ sơ. Nếu hồ sơ có tags -> find tags and deleted*/
    //@ts-ignore
    await this._storage.forEach((hoso, k) => {
      if (k.startsWith(CAIDAT.skey)) {
        if (hoso.tags && hoso.tags?.has(tag)) {
          this.tagsList.delete(tag)
          hoso.tags.delete(tag)
        }
      }
    })
    this.updateTagsList()
  }
  //@ts-ignore
  deleteCombineTags(biglist) {
    //@ts-ignore
    this._storage.set(CAIDAT.tags.savedBigList, biglist)
  }
  async updateTagsList() {
    //@ts-ignore
    await this._storage.set(CAIDAT.tags.tagsList, this.tagsList)
  }
  renameTag(old: string, newname: string) {
    this.tagsList.delete(old)
    this.tagsList.add(newname)
    let x = this.filterBigList.map((val, ind) => {
      console.log(val)
      let value = ""
      val.split(",").forEach((val1, ind1) => {
        if (val1 == old) {
          console.log(val1)
          value = value + (value==""? "": ",") + newname
        } else value = value + (value==""? "": ",") + val1
      })
      return value
    })
    this.filterBigList = x
    this.updateTagsList()
    //@ts-ignore
    this._storage.set(CAIDAT.tags.savedBigList, this.filterBigList)
  }
  async filterSaveToStorage(filterList: string[]) {
    let stringFilter = filterList.toString()
    // filterBigList = ["sử,địa", "văn,anh"]
    if (!this.filterBigList.includes(stringFilter)) {
      this.filterBigList.push(stringFilter)
      //@ts-ignore
      this._storage.set(CAIDAT.tags.savedBigList, this.filterBigList)
    }
  }
}
