import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CongcuforallTsService {

  constructor(private http: HttpClient) { }
  
  async currentTimeFromWorld() {
    return new Promise((resolve, reject) => {
      this.http
        .get('https://worldtimeapi.org/api/timezone/Asia/Ho_Chi_Minh')
        .subscribe({
          next: (data: any) => {
            const utcDatetime = data.utc_datetime;
            // console.log(utcDatetime); // Output the utc_datetime
            resolve(utcDatetime);
          },
          error: (error) => {
            reject(error); // Reject the promise with the error
          },
        });
    });
  }
  private objectsAreDifferent(obj1: any, obj2: any): boolean {
    const keys1 = Object.keys(obj1);
    const keys2 = Object.keys(obj2);
  
    if (keys1.length !== keys2.length) {
      return true;
    }
  
    for (const key of keys1) {
      if (obj1[key] !== obj2[key]) {
        return true;
      }
    }
  
    return false;
  }
}
