// @ts-nocheck
import { BehaviorSubject } from "rxjs"
import { HOSO } from "../hoso.model"

export class XuLyDuLieuXuatAnki {
    data!: any[] // data: mảng 2 chiều của sheet
    hoso: HOSO
    replaceArray!: any[]
    constructor(getSheetValues?: any[] , hoso?: HOSO) {
        this.data = getSheetValues!
        this.hoso = hoso!
        this.replaceToArray()
        // console.log(this.replaceArray)
    }
    public loadinggetcolumns$!: BehaviorSubject<boolean>
    /**
     * Biến array sheet thành 1 mảng đủ cột, đủ
     */
    htmlArray() {
        // Loop quanh mảng 2 chiều, trả về mảng 2 chiều đã xử lý html
        // console.log("@TEST Nhận data oke", this.data)
        let html: (any[] | undefined)[]
        html = this.data.map((valueRow, indexRow) => {

            // Index == 0 thì bỏ qua, Bỏ qua hàng 0
            if (indexRow != 0) {
                // console.log("@TEST loop theo từng hàng", indexRow, valueRow)
                // console.log(valueRow, index)
                // Đưa mỗi hàng (Row) bỏ vào hàm để xử lý từng cột
                let rowedited = this.htmlEachColumn(valueRow)
                return rowedited
            }


        })
        // html là array các cột đã đầy đủ
        // Giờ sẽ loop qua từng cột để check xem nó có copy xuống không
        // console.log("html", html)
        html = this.copyXuongExe(html)
        // console.log(html)
        return (html)
    }
    /**
     * Xử lý mỗi hàng trong row
     * @param row giá trị array của mỗi hàng
     * @returns hàng đó đã html và replace
     */
    htmlEachColumn(row: any[]) {
        // Thêm hàng tên cột vào cuối cùng row.at(-1) để định danh
        // map mỗi cột trong row xử lý và trả về giá trị
        let html = row.map((valCell, indexCol) => {
            if (indexCol == 0) {
                return undefined
            }
            // console.log(this.hoso.columns[indexCol - 1])
            // console.log(this.hoso.columns, indexCol - 1)
            let isReplaceCot = this.hoso.columns[indexCol - 1].replace
            if (typeof valCell == "object") {
                // object thì lấy richText thêm class vào từng span
                // console.log(value)
                // https://i.imgur.com/Y0mfwUn.png richText
                // https://i.imgur.com/wnpzCBg.png font
                let currentClozeCount = 0
                // console.log(valCell)
                /** Nếu valCell có hyperlink thì nó không chỉ là richText, mà nó sẽ là .text.richText
                 * https://i.imgur.com/Llh2523.png
                 */
                if (valCell.hasOwnProperty("hyperlink")) {
                    valCell = valCell.text
                }
                /**
                 * Nếu Extranote chỉ có 1 link thôi -> sẽ báo lỗi
                 *  hyperlink: "https://i.imgur.com/sjbo5CW.png"
                    text: "https://i.imgur.com/sjbo5CW.png"
                 */
                let newVal
                if (valCell.richText) {
                    // @CLOZE Group Bắt đầu với dấu phẩy sau đó là 1 hoặc nhiều số
                    let patternTestClozeGroupPrefix = /^,\d+/
                    let patternTestClozeGroupSuffix = /,\d+$/
                    // console.log(valCell)

                    newVal = valCell.richText.map((val, ind) => {
                        let spanclass = ""
                        let fontcolor = ""
                        // https://stackoverflow.com/questions/51037435/convert-argb-to-rgba-format
                        /*  var a = "#AABBCCDD";
                            var b = "#"+a.slice(3,9)+a[1]+a[2];
                        */
                        //    console.log(val.font)
                        if (val.font) {

                            for (const [k, v] of Object.entries(val.font)) {
                                //    console.log(k, v)
                                if (v == true) {
                                    spanclass = spanclass.concat(" ", k)
                                }
                                if (k == "color") {
                                    if (v["argb"] != undefined) {
                                        let a = "#" + v["argb"]
                                        let argbToRGBA = "#" + a.slice(3, 9) + a[1] + a[2];
                                        fontcolor = ` style="color: ${argbToRGBA}"`
                                    }
                                }
                                // Xử lý cloze cho bold của cột đầu tiên
                                //    @CLOZE : xử lý cloze with BOLD
                                if (k == "bold" && indexCol == 1) {
                                    if(val.text.trimEnd().slice(-1) == "_"){
                                        val.text = val.text.trimEnd().replace(/_+$/, "")
                                        continue;
                                    }
                                    // Thỏa đk @CLOZE group (not null)
                                    let haveClozegroup = val.text.trimStart().match(patternTestClozeGroupPrefix)
                                    let haveClozegroupSuffix = val.text.trimEnd().match(patternTestClozeGroupSuffix)
                                    if (haveClozegroupSuffix) {
                                        let prespace =""
                                        let sufspace = ""
                                        if(val.text.slice(0,1) == " "){
                                            prespace = " "
                                        }
                                        if(val.text.slice(-1) == " "){
                                            sufspace = " "
                                        }
                                        //Nếu có cả 2 thằng thì xóa luôn thằng trước
                                        if (haveClozegroup) {
                                            val.text = val.text.slice(haveClozegroup[0].length)
                                        }
                                        // Slice từ vị trí thứ 2, để bỏ dấu , ở đầu
                                        val.text = `{{c${haveClozegroupSuffix[0].slice(1)}::${val.text.slice(0, val.text.length - haveClozegroupSuffix[0].length)}}}`
                                        val.text = prespace + val.text + sufspace
                                    }
                                    else if (haveClozegroup) {
                                        // Slice từ vị trí thứ 2, để bỏ dấu , ở đầu
                                        val.text = `{{c${haveClozegroup[0].slice(1)}::${val.text.slice(haveClozegroup[0].length)}}}`
                                    }
                                    else if (this.hoso.clozeAllInOne) {
                                        /**
                                         * Tất cả c1, nếu
                                         */
                                        val.text = `{{c1::${val.text}}}`
                                    }
                                    else {
                                        currentClozeCount += 1
                                        val.text = `{{c${currentClozeCount}::${val.text}}}`
                                    }

                                }

                            }
                        }
                        let text = `<span${spanclass != "" ? " class='" + spanclass + "'" : ``}${fontcolor != "" ? fontcolor : ""}>${this.replaceValue(val.text, isReplaceCot, indexCol)}</span>`
                        // console.log(text)
                        return text
                    })
                    newVal = newVal.join("")

                } else {
                    newVal = this.replaceValue(valCell, isReplaceCot, indexCol)
                    // console.log(valCell, newVal)
                }
                // Tạo autocloze cho card không có bold
                if (indexCol == 1) {
                    newVal = this.autocloze(newVal)
                }
                // console.log("Newvalue", newVal)
                return newVal

            } else if (typeof valCell == "string") {
                // Nếu cột 1 trống, autocloze
                if (indexCol == 1) {
                    valCell = this.autocloze(valCell)
                }
                // Nếu ô đó Là string -> replace các giá trị, cũng như; link ảnh
                return this.replaceValue(valCell, isReplaceCot, indexCol)
            }


        })
        // console.log(html)
        return html
    }
    autocloze(valCell: string) {
        if (valCell.indexOf("{{c1::") == -1) {
            return valCell + `
<span class="hide">{{c1::autocloze}}</span>`
        } else return valCell
    }

    /**
     * chạy quanh các cột check thử nếu có copy xuống thì chạy, còn không thì thôi
     * @param html Đây là mảng 2 chiều của ô đã xử lý html và replace xong
     */
    copyXuongExe(html: any[][]) {
        this.hoso.columns.map((value, index) => {
            // index > 1: tức bỏ qua front, và back
            if (index > 1) {
                let currentColumnsValue = ""
                html.forEach((row, indRow) => {
                    /**
                     * Xử lý từng cột ở mỗi hàng của note @@
                     */
                    if (row[index + 1] != undefined) {
                        currentColumnsValue = row[index + 1]
                    } else {
                        if (value.copyxuong) {
                            // Nếu cột không có giá trị + có copyxuong=true
                            row[index + 1] = currentColumnsValue
                        } else {
                            /* Tự thêm 1 cột khi không có giá trị đó
                            Nếu cột không có giá trị + copy xuống = false
                            thì gián giá trị rỗng (Lỗi AnkiDroid không nhập được undefinded)*/
                            row[index + 1] = ""
                        }
                    }
                })
            }
        })
        return html
    }
    /** Xử lý replace, tạo array replace
         * Chỉ lấy duy nhất dấu bằng "=" đầu tiên
         */
    replaceToArray() {
        let newlinewith = "##"
        this.replaceArray = this.hoso.replace.split("\n")
            .map((hang, ind) => {
                hang = hang.replace(/\s+/, " ")
                if (hang.trim() != "") {
                    let tach = hang.split("=")
                    return [tach[0].replace(newlinewith, "\n"), tach[1].replace(newlinewith, "\n")]
                }
            })
        // console.log(this.replaceArray)
        this.replaceArray = this.replaceArray.filter(ele => {
            return typeof ele == "object"
        })
    }
    /**
     * 
     * @param value giá trị text, chứa link img cũng như các vị trí replace
     * @returns text đã replace theo biến replace, và hình ảnh
     */
    replaceValue(value: string, isReplaceCot: Boolean = false, indexCol: number = 3) {
        let finalValue = value
        // console.log(finalValue)
        // console.log(this.replaceArray)
        this.replaceArray.forEach(element => {
            if (finalValue.includes(element[0])) {
                finalValue = finalValue.replaceAll(element[0], element[1])
            }
        });
        finalValue = finalValue.replace(/\n/g, "<br/>")
        /** 
         * 1. Thay thế newline: mặc định all
         * 2. Thay thế ảnh: có replace
         * 3. Thay thế youtube link: có replace
         * 4. Thay thế mp3? audio
         */
        // console.log(value)
        if (isReplaceCot || indexCol <= 2) {
            finalValue = finalValue
                .replace(/(https?:\/\/.*\.(?:png|jpg|gif|jpeg|webp))/g, (match) => {
                    return `<img class="image" src="${match}" referrerpolicy="no-referrer"></img>`
                })
                .replace(/(http:|https:)?\/\/(www\.)?(youtube\.com|youtu\.be)\/(watch)?(\?v=)?(\S+)?/g, (match) => {
                    return `<iframe class="youtube" width="560" height="315" src="https://www.youtube.com/embed/${this.youtube_parser_getid(match)}"
                title="YouTube video player by drquochoai" frameborder="0" allow="accelerometer; autoplay; clipboard-write;
                encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`
                })
        }

        return finalValue
    }
    youtube_parser_getid(url: string) {
        var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
        var match = url.match(regExp);
        return (match && match[7].length == 11) ? match[7] : false;
    }

}
